import { describe, PropTypes } from 'react-desc';

export default (function (Element) {
  var DocumentedElement = describe(Element).description('A page header component.\n      ').usage('\n      $ npm install grommet-controls\n \n      import { Header } from \'grommet-controls\';\n\n      <Header>\n        ...\n      </Header>  \n    ');

  DocumentedElement.propTypes = {
    position: PropTypes.oneOf(['fixed', 'absolute', 'sticky', 'static', 'relative']).description('Position property for the header element').defaultValue('relative'),
    zIndex: PropTypes.number.description('z-index for the header.').defaultValue(10)
  };

  return DocumentedElement;
});