import { describe, PropTypes } from 'react-desc';

export default (function (Element) {
  var DocumentedElement = describe(Element).description('A masked color entry field, with a drop button to select a color.\n      All properties of MaskedInput apply\n      ').usage('\n      $ npm install grommet-controls\n \n      import { ColorInput } from \'grommet-controls\';\n\n      <ColorInput colors={...} value={...} />\n    ');

  DocumentedElement.propTypes = {
    colors: PropTypes.object.isRequired.description('Color palette for the user to choose a color from.')
  };

  return DocumentedElement;
});