import { describe, PropTypes } from 'react-desc';

export default (function (Element) {
  var DocumentedElement = describe(Element).description('A Button with a hover effect, all properties of Button apply.\n      ').usage('\n      $ npm install grommet-controls\n \n      import { IconButton } from \'grommet-controls\';\n\n      <IconButton icon=\'...\' />\n    ');

  DocumentedElement.propTypes = {
    icon: PropTypes.element.description('Icon element to place in the button.')
  };

  return DocumentedElement;
});